/* HEADER SHOW SCROLL */
.activenav {
    position: fixed;
    display: flex;
    width: 100%;
    -webkit-animation:slide-down .7s ease forwards;
            animation:slide-down .7s ease forwards;
    box-shadow: 0px 13px 25px -12px rgb(0 0 0 / 25%);
    z-index: 999 ;
    top: 0px;
    left: 0;
    font-size: 11px;
    align-items: center;
    padding:13px 100px;
  }
  .activenav .lgg {
      width: 130px;
  }
  .activenav .header-cart-tab {
      display: none;
  }
  .activenav .header__wrapper-left a{
      font-size: 11px;
  }
  .activenav .header__wrapper-right {
      margin-top: 7px;
      margin-right: 40px;
  }
  @-webkit-keyframes slide-down {
    0% {
        opacity: 0;
        margin-right: 100px;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    } 
    100% {
        opacity: 1;
        margin-right: 0;
        transition: .4s linear;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    } 
  }
  @keyframes slide-down {
    0% {
        opacity: 0;
        margin-right: 100px;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    } 
    100% {
        opacity: 1;
        margin-right: 0;
        transition: .4s linear;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    } 
  }
