.swiper-container {
  width: 93.5%;
  height: 790px;
  margin-right: 0 !important;
  position: relative;
}
.container__swiper {
  background-image: url(../../../assets/images/bgsl.png);
  background-position: center;
  background-size: cover;
}
.swiper-pagination-bullets {
  display: flex ; 
  position: absolute;
  bottom: 130px !important;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  display: flex;
  background: transparent;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center ;
  margin-top: 40px;
}
.olst{
  position: absolute;
  top: 115px;
  font-family: josefin sans,sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  color: #1d1d1d;
  font-size: 29px;
  line-height: 1.55em;
  letter-spacing: .2em;
  margin: 25px 0;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}
.ols {
  position: absolute;
  top: 110px;
  display: block;
  margin: 0 0 12px;
  font-family: eb garamond,serif;
  font-size: 18px;
  font-style: italic;
  color: #8b8b8b;
}
.swiper-slide img {
  display: block;
  width: 366px;
  height: 366px; 
  object-fit: cover;
}

.swiper__content-h5 {
  position: relative;
  cursor: pointer;
  transition: .1s linear;
}
.swiper__content-h5 p, .swiper__content-h5 h6 {
  position: absolute;
  bottom: 10px;
}
.swiper__content-h5 h6 {
  left: 40px;
  font-family: josefin sans,sans-serif;
  font-weight: 600;
  color: #1d1d1d;
  font-size: 13px;
  letter-spacing: .2em;
  margin: 17px 0;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
  bottom: 40px;
}
.swiper__content-h5 p {
  right: 40px;
  bottom: 31px;
  font-family: eb garamond,serif;
  font-size: 17px;
  font-weight: 400;
  margin: 17px 0;
  font-style: italic;
  color: #1d1d1d;
  transition: .1s linear;
}
.tocart {
  position: absolute;
  bottom: 40px;
  opacity: 0 !important;
  border-bottom: 1px solid #1d1d1d;
  right: 35px;
  transition:  .1s linear;
  font-size: 16px;
  font-family: eb garamond,serif;
  font-weight: 400;
  font-style: italic;
  color: #1d1d1d;
  letter-spacing: .4px;
  line-height: 1.4em;
  text-transform: capitalize;
}
.swiper__content-h5:hover .tocart {
  margin-bottom: 13px;
  opacity: 1 !important;
  transition: .1s linear;
}
.swiper__content-h5:hover p {
  margin-bottom: 35px;
  opacity: 0;
  transition:  .1s linear;
}

.swiper__content-h5 span {
  bottom: 35px;
  position: absolute;
  left: 40px;
  color: #8b8b8b;
  letter-spacing: .4px;
  font-family: sans-serif;
  font-size: 16px;
  line-height: 1em;
}

/* resposive */
@media only screen and ( max-width: 1024px) {
  .swiper-slide img {
    width: 320px;
    height: 320px; 
  }.swiper-slide {
    margin: 0 40px !important;
  }
}
@media only screen and ( max-width: 768px) {
  .swiper-slide img {
    width: 318px;
    height: 318px; 
  }.swiper-slide {
    margin: 0 80px !important;
  }
}
@media only screen and ( max-width: 600px) {
.swiper-slide {
    margin: 0 100px !important;
  }
}
@media only screen and (max-width:376px) {
  .olst{
    margin-left: 10px !important;
  }
  .ols {
    margin-left: 10px !important;
  }.swiper-slide {
    margin: 0 130px !important;
  }
}@media only screen and ( max-width: 420px) {
  .swiper-slide {
      margin: 0 130px !important;
    }
  }