 .content__animation {
    margin:10px auto;
    display: flex;
    margin-top: 110px;
    justify-content: center;
}
.content__animation-wrapper {
    display: flex; 
    overflow: hidden;
}
.content__animation-wrapper p {
    font-family: the gwathmey,serif;
    font-size: 60px;
    margin-left: -80px;
    line-height: 2em;
    color: #bdced3;
}
.content-child {
    margin-left: 30px;
}
.testi {
    margin-left: 30px;
    margin-bottom: 15px;
}
.section__wrapper {
    height: 260px;
    transition: .3s ease-in;
}
.content-child {
    height: 140px;
    overflow: hidden;
    display: flex;
    margin-left: 40px;
    margin-top: -20px;
}
.content-child section {
    overflow: hidden;
    margin: 40px 0;
}
.content-child h3 {
    font-family: josefin sans,sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    color: #1d1d1d;
    font-size: 19px;
    line-height: 1.85em;
    letter-spacing: .18em;
    word-wrap: break-word;
}
.next-btn,.pre-btn {
    width: 40px;
    height: 40px;
    cursor: pointer;
    border-radius: 50%;
}
.director-btn {
    display: flex;
    margin: 0px;
    height: 190px;
    flex-direction: column;
    z-index: 1;
    margin-top: 67px;
}
.director-btn button {
    width: 40px;
    height: 40px;
    outline: none;
    border: none;;
    cursor: pointer;
    border-radius: 50%;
}
.wrapper-test {
    margin-left: 120px;
    display: flex;
    width: 800px;
    height: 100px;
    justify-content: space-between;
}
.section h6 {
    font-weight: 500;
    font-family: sans-serif;
    font-size: 16px;
    color: #8B8B8B;
    margin-top:9px;
    letter-spacing: .3px;
}

.check {
    color: #1d1d1d !important;
    font-size: 15px;
    width: 37px;
    height: 37px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* transition: .3s ease-in; */
    border: 2px solid #B2B2B2;
}
.oknha {
    background-color: transparent;
    color: #B7B7B7;
    /* width: 37px;
    height: 37px; */
    display: flex;
    margin-bottom: 10px;
    justify-content: center;
}
.oknhaa {
    margin-bottom: 10px;
}
.director-btn div {
    background-color: transparent;
    color: #B7B7B7;
    width: 37px;
    height: 37px;
    display: flex;
    margin-bottom: 10px;
    cursor: pointer;
    justify-content: center;
}

.sliderpath path:nth-child(1) {
    -webkit-animation: qode-float-move 3s .1s infinite;
    animation: qode-float-move 3s .1s infinite;
    animation-duration: 3s;
    animation-timing-function: ease;
    animation-delay: 0.1s;
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation-fill-mode: none;
    animation-play-state: running;
    animation-name: qode-float-move;
}
.cirani {
    animation: qode-float-move 5s .3s infinite;
    animation-duration: 5s;
    animation-timing-function: ease;
    animation-delay: 0.3s;
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation-fill-mode: none;
    animation-play-state: running;
    animation-name: qode-float-move;
}
path:nth-child(3) {
    animation-duration: 4s;
    animation-timing-function: ease;
    animation-delay: 0.5s;
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation: qode-float-move 4s .5s infinite;
    animation-fill-mode: none;
    animation-play-state: running;
    animation-name: qode-float-move;
}
@keyframes qode-float-move {
    0%, 100% {
        transform: translate3d(0,0,0);
    }
    
    50% {
        transform: translate3d(0,10px,0);
}
} 

/* ------RESPONSIVE----- */
@media only screen and (max-width: 1024px) {
   .content-child h3 {
       max-width: 600px;
   }
   .director-btn {
       margin-right: 90px;
   }
   .content__animation {
       margin-left: 80px;
   }
   .wrapper-test {
       width: 670px;
   }.section__wrapper {
    height: 285px;
}
.content-child {
    height: 150px;
    margin-left: 40px;
}
.content-child section {
    margin:20px 0;
}
} 
@media only screen and (max-width: 768px) {
    .content-child h3 {
        max-width: 300px;
    }
    .content__animation {
        margin-left: 30px;
    }.section__wrapper {
     height: 365px;
 }
 .wrapper-test {
     width: 290px;
 }
 .director-btn {
     margin-right: 20px;
 }
 .content-child {
     height: 180px;
 }
 .content-child section {
     margin:20px 0;
 }
 .content-child h3 {
    font-size: 17px;
    line-height: 1.85em;
    letter-spacing: .1em;
}
 } 
 @media only screen and (max-width: 415px) {
.content__animation-wrapper {
    flex-direction: column; 
}
.content__slide {
    display: flex;
    flex-direction: column;
    margin-left: 0px;
    position: relative;
    padding-bottom: 50px;
}
.content__animation {
    margin-left: 5px;
}
.director-btn {
    position: absolute;
    top: 180px;
    display: flex !important;
    flex-direction: row !important;
}

 } 